@media only screen and (max-width: 1024px) {
  .garage-form__add-baik,.garage-form__in-garage {
    width: 50%;
  }
  .garage-form__add-baik_form{
    width: 90%;
  }
  .garage-form__in-garage{
    .bike-info{
      text-align: left;
    }
    .moto-photo{
      img{
        width: auto;
        height: auto;
      }
    }
  }
}
