.authorization{
  float: left;
  width: 100%;
  position: relative;
  margin: 25px 0;
 
    .authorization_form_cont{
    h4{
      color: #fff;
      text-align: center;
      font-family: $gill;
      font-size: 22px;
    }
      p{
        font-family: $gill;
        color: #fff;
        font-size: 16px;
        margin-top: 0;
        padding-left: 15px;
      }
      background-color: #282828;
      border-radius: 10px;
      width: 30%;
      margin: 0 auto;
      padding: 49px;
      input{
        outline: none;
      }
      border: solid 1px #545454;
      font-family: $gill;
      .regist_{
        float: left;
        color: #fff;
        text-decoration: underline;
        font-family: $gill;
      }
      .forgot_your_pass_{
        float: right;
        color: #fff;
        text-decoration: underline;
        font-family: $gill;
      }
    .login{
      margin: 50px auto 0;
      float: none;
      display: inherit;
      height: 40px;
      outline: none;
      width: 70%;
    }
    }
.after-form{
  text-align: center;
  margin:33px;
  a{
    font-family: $gill;
    display: block;
  }
}

}