.garage-form{
  width: 100%;
  float: left;
  display: block;
  position: relative;
  text-align: center;
  padding-bottom: 40px;
  &-title{
    text-align: left;
    color: #1d1d1d;
    font-size: 20px;
    font-family: $gill;
  }
  &__add-baik{
    float: left;
    width: 30%;
    position: relative;
    &_form{
      width: 100%;
      float: left;
      display: block;
      position: relative;
      &_input{
        width: 100%;
        float: left;
        height: 31px;
        background-color: #fff;
        border: 1px solid rgba(156, 156, 156, 0.38);
        border-radius: 4px;
        margin: 6px 0;
        padding-left: 15px;
        box-sizing: border-box;
      }
      textarea{
        width: 70%;
        float: left;
        height: 50px;
        background-color: #fff;
        border: 1px solid rgba(156, 156, 156, 0.38);
        border-radius: 4px;
        margin: 6px 0;
        padding-left: 15px;
        box-sizing: border-box;
        resize: none;
      }
      select{
        float: left;
        display: inline-block;
        width: 70%;
        height: 31px;
        background-color: #fff;
        border: 1px solid rgba(156, 156, 156, 0.38);
        border-radius: 4px;
        margin: 6px 0;
        padding-left: 15px;
        box-sizing: border-box;

      }
      &_item{
        width: 100%;
        float: left;
        padding-left: 0;
        margin: 10px 0;

        label{
          width: 100%;
          float: left;
          .item_marker{
            float: left;
          }
          .item_title{
            width: 80%;
            float: left;
            margin: 0;
            text-align: left;
            padding-left: 10px;
            box-sizing: border-box;
            color: #191919;
            font-family:$gill;
            font-size: 14px;
            line-height: 20px;
            font-style: normal;
          }
        }
        input[type="radio"] {
            display:none;
        }
        input[type="radio"] + label {
          display: block;
          -webkit-user-select: none;
             -moz-user-select: none;
              -ms-user-select: none;
                  user-select: none;
        }

        input[type="radio"] + label span.item_marker {
            display: inline-block;
            width:19px;
            height:19px;
            vertical-align:middle;
            cursor:pointer;
            -moz-border-radius:  50%;
            border-radius:  50%;
        }

        input[type="radio"] + label span.item_marker {
             background-color: #c6ccd2;
        }

        input[type="radio"]:checked + label span.item_marker{
             background-color: $o;
             background-image: url("../img/Check-Icon.png");
             background-position: center;
             background-repeat: no-repeat;
             color: $o;
        }

        input[type="radio"] + label span.item_marker,
        input[type="radio"]:checked + label span.item_marker {
          -webkit-transition:background-color 0.4s linear;
          -o-transition:background-color 0.4s linear;
          -moz-transition:background-color 0.4s linear;
          transition:background-color 0.4s linear;
        }

      }
      &_knopka{
        float: left;
        display: inline-block;
        min-width: 200px;
        height: 38px;
        background-color: #ff6000;
        border: 1px solid rgba(156, 156, 156, 0.38);
        border-radius: 3px;
      }
    }
  }
  &__in-garage{
    width: 50%;
    float: left;
    .bike-info{
      text-align: left;
    }
    .moto-photo{
      img{
        width: auto;
        height: 100%;
      }
    }
  }
}
.block-add-file{
  width: 100%;
  float: left;
  display: inline-block;
  margin: 20px 0;
}
.file_upload{
    float: left;
    display: block;
    position: relative;
    overflow: hidden;
    font-size: 18px;
    height: 29px;
    min-width: 143px;
    background-color: #9b9898!important;
    border-radius: 10px;
  cursor: pointer;
}
.file_upload .button, .file_upload > mark{
    display: block;
    cursor: pointer
}
.file_upload .button{
    float: left;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    width: 8em;
    height: 100%;
    margin: 0;
    padding: 0;
    font-size: 18px;
    font-family: $gill;
    text-align: center;
    cursor: pointer;       /* example */
}
 .file_upload > mark{
     background: transparent;     /* example */
     padding-left: 1em            /* example */
}

.file_upload input[type=file]{
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0
}
