.authorization{
  float: left;
  width: 100%;
  position: relative;
  margin: 25px 0;
 
    .authorization_form_cont{

      background-color: #282828;
      border-radius: 10px;
      width: 30%;
      margin: 0 auto;
      padding: 49px;
      input{
        outline: none;
      }
      border: solid 1px #545454;
      font-family: $gill;
      .regist_{
        float: left;
        color: #fff;
        text-decoration: underline;
        font-family: $gill;
      }
      .forgot_your_pass_{
        float: right;
        color: #fff;
        text-decoration: underline;
        font-family: $gill;
      }
    .login{
      width: 75%;
      margin: 50px auto 0;
      float: none;
      display: inherit;;
    }
    }
.after-form{
  text-align: center;
  margin:33px;
  a{
    font-family: $gill;
    display: block;
  }
}

}