.settings-profile{
  width: 100%;
  position: relative;
  padding-bottom: 88px;
  float: left;
    .settings-mnu{
      width: 33.3%;
      float: left;
      padding-top: 22px;
      position: relative;
      h3{
        font-family: $gill;
        font-size: 24px;
        text-transform: uppercase;
        color: #191919;
        margin-bottom: 60px;
        &:after{
          content: "";
          position: absolute;
          top: 62px;
          left: 0;
          width: 115px;
          height: 0;
          border-bottom: 2px dashed #ff6000;
        }
      }
    &__items{
      display: block;
      font-family:$gill;
      font-size: 18px;
      text-transform: uppercase;
      color: #191919;
      width: 80%;
      text-align: left;
      border-radius: 0;
      margin-left: 0;
      padding-left: 11px;

    }
    }
.settings-profile-form{
  width: 66.6%;
  float: right;
  padding-top: 73px;
  h4{
    font-family: $gill;
    font-size:18px;
    color: #191919;
  }

  .settings-profile-form_item{
    width: 70%;
    height: 38px;
    position: relative;
    border: 1px solid rgba(156, 156, 156, 0.38);
    border-radius: 8px;
    outline: none;
    margin: 5px;
    padding-left: 10px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    font-family: $gill;
    font-size: 13px;
    resize: none;

  }
  .settings-profile-form_item-textarea{
    height: 80px;
    padding-top: 7px;
    width: 70%;
    position: relative;
    border: 1px solid rgba(156, 156, 156, 0.38);
    border-radius: 8px;
    outline: none;
    margin: 5px;
    padding-left: 10px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    font-family: $gill;
    font-size: 13px;
    resize: none;
  }
.subm{
  border: transparent;
  display: block;
  margin-top: 0;
  width: 27%;
  cursor: pointer;
}

  }


}