.kabinet-main-filter{
  margin: 40px 0;
}
.nav-button{
  width: 100%;
  float: left;
  text-align: center;
  position: relative;
  padding: 30px 0;
  box-sizing: border-box;
  a{
    display: inline-block;
    color: #fff;
    font-size: 18px;
    font-family: $gill;
    float: none;
    border-radius: 10px;
    padding: 7px 20px;
    box-sizing: border-box;
    margin-left: 88px;
    &:nth-of-type(even){
      background-color: #282828;
      &:hover {
          transition: all .3s linear;
          background-color: #0f0f0f;
      }
    }
    &:nth-of-type(odd){
      background-color: #ff6000;
      &:hover {
          background-color: #cc4d00;
          transition: all .3s linear;
      }
    }
    &:nth-of-type(3){
      background-color: #9b9898;
      &:hover {
          background-color: #928c8c;;
          transition: all .3s linear;
      }
    }
    &:first-of-type{
      margin-left: 0;
    }

  }
}

.kabinet__tabs{
  width: 100%;
  float: left;
  position: relative;
  background-color: #fff;
  .title__tabs{
    width: 100%;
    float: left;
    position: relative;
    background-color: #fff;

    ul{
      padding: 0;
      // background-color: #f4f4f4;
      margin: 0;
      li{
        width: 33.3%;
        float: left;
        list-style-type: none;
        // background-color: #f4f4f4;
        a{
          color: #282828;
          font-family: $fow;
          font-size: 36px;
          padding: 60px 0;
          box-sizing: border-box;
          text-transform: uppercase;
          text-decoration: none;
          background-color: #f4f4f4;
          span{
            border-bottom: 1px dashed $o;
            // min-width: 20%;
          }
        }
        &:nth-of-type(even){
          a{
            background-color: #fff!important;
          }
        }
      }
    }
  }

  .kabinet__tabs_target{
    width: 100%;
    float: left;
    display: inline-block;
    text-align: center;
  }


  .box-connent{
    width: 100%;
    float: left;
    position: relative;
  }

  .kabinet__tabcontent{
    width: 100%;
    float: left;
    padding: 0 0 50px;
    box-sizing: border-box;
  }

  .five{
    display: block;
  }
  .one,.three, .four, .two, .six{
    display: none;
  }



  .kabinet__tabs_active{
    display: block;
    span{
      color: $o;
    }
  }

  .article-title{
    margin: 0;
    font-family: $gill;
    font-size: 24px;
    color: #000;
    text-transform: uppercase;
  }
  .item-article{
    width: 60%;
    float: left;
    margin: 15px 0;
    a{
      width: 100%;
      display: inline-block;
      float: left;
      font-family: $gill;
      font-size: 24px;
      color: #000;
      &:hover{
        color: $o;
      }
    }
    span{
      display: inline-block;
      float: left;
      min-width: 10%;
      color: #9b9898;
      font-size: 20px;
    }
  }

}

.kabinet-photo{
  width: 100%;
  float: left;
  position: relative;
  background-color: #f4f4f4;
  padding: 70px 0;
  box-sizing: border-box;
  h2{
    display: inline-block;
    width: 100%;
    text-align: center;
    font-size: 36px;
    color: #282828;
    font-family: $fow;
    text-transform: uppercase;
    position: relative;
    &::after{
      content: "";
      border-bottom: 1px dashed $o;
      display: inline-block;
      position: absolute;
      min-width: 11%;
      left: 45%;
      bottom: 0;
    }
  }
  .gallery{
    .gallery-items{
      width: 14.5%;
      height: 177px;
      margin: 12px;
      display: inline-block;
      float: left;
      img{
        height: 100%;
        width: 100%;
        object-fit: cover;
      }
    }

  }
  .gallery-albums{
    width: 100%;
    float: left;
    position: relative;
    .link-albums{
      height: 177px;
      margin: 12px;
      width: 18.7%;
      float: left;
      background-color: #686bb1;
      color: #fff;
      font-size: 16px;
      text-align: center;
      line-height: 170px;
    }

  }
}
.add-photos{
  width: 14.5%;
  height: 177px;
  margin: 12px;
  text-align: center;
  display: inline-block;
  background-color: #272727;
  color: #fff;
  font-size: 16px;
  position: relative;
  padding-top: 40px;
box-sizing: border-box;
  .plus{
    cursor: pointer;
    height: 21px;
    position: absolute;
    width: 21px;
    bottom: 35%;
    left: 40%;
    &:before,
    &:after{
      background: #fff;
      content: '';
      height: 5px;
      left: 0;
      position: absolute;
      top: 0;
      width: 35px;
      transition: transform 500ms ease;
    }
    &:after {
      transform-origin: center;
    }
    &:before {
      transform: rotate(90deg);
    }
  }
}
