@media only screen and (max-width: 1024px) {
  .navigation__btn{
    float: left;
    display: block;
    left: 2%;
  }
  .navigation__search{
    float: right;
    right: 2%;
    position: absolute;
    top: 0;
    margin: 15px 0;
    width: 80%;
  }
  .navigation__menu_mobile{
    width: 100%;
    li{
      width: 100%;
      text-align: center;
    }
  }
  .navigation__menu_1 {
    display: none;
  }
  .navigation__menu_2{
    display: none;
  }
  .route__text {
		width: 46%;
	}
	.route__destination {
		width: 27%;
	}
	.route__go {
		width: 27%;
		padding-left: 30px;
		a.button {
			font-size: 12px;
			float: right;
      margin-top: 183px;
      right: 82%;
      min-width: 263px;
      padding: 5px 0;
			-webkit-align-self: flex-end;
			        align-self: flex-end;
			.icon_road {
				width: 23px;
				height: 27px;
		    }
		}
	}
  .main__top-button{
    .show-button{
      min-width: 32%;
      font-size: 30px;
    }
  }
  .main-page__tabs, .main-page__tabs_first {
    .title__tabs{
      padding: 0;
      ul{
        li{
          a{
            font-size: 30px;
            padding: 30px 0;
          }
        }
      }
    }
  }
  .main-page__gallery{
    .gallery{
      .gallery-items{
        width: 21.7%;
      }
    }
  }
  .main-page__tabs .page__tabcontent .column {
      width: 42.6%;
  }
}
@media only screen and (max-width: 770px) {

}

@media only screen and (max-width: 736px) {
  .navigation__btn{
    float: left;
    display: block;
  }
  .navigation__search{
    float: right;
    right: 2%;
    position: absolute;
		top: 0;
		margin: 15px 0;
    width: 70%;
  }
  .navigation__menu_mobile{
    li{
      width: 100%;
      text-align: center;
    }
  }
  .navigation__menu_1 {
		display: none;
	}
  .navigation__menu_2{
    display: none;
  }
  .route__text {
		width: 100%;
	}
  .route__destination {
    width: 100%;
  }
  .route__go {
    width: 100%;
    padding-left: 0;
    // a.button {
    //   margin-top: 15px;
    // }
    a.button.button_orange {
        margin: 0 auto;
        right: 0;
    }
  }
  .main__top-button{
    .show-button{
      font-size: 25px;
      width: 100%;
      padding: 15px 0;
      cursor: pointer;
    }
  }
  .main-page__tabs, .main-page__tabs_first {
    .title__tabs{
      padding: 0;
      ul{
        li{
          width: 100%;
          a{
            font-size: 30px;
            padding: 10px 0;
          }
        }
      }
    }
    .box-connent{
      .page__tabcontent, .page__tabcontent_first{
        .column{
          width: 100%;
          margin: 10px 0;
        }
        .news__box{
          .news__item{
            flex-basis: 100%;
          }
        }
        .howitworks__flex{
          justify-content: center;
        }
        .add-article{
          min-width: 70%;
          background-color: #ff6000;
          color: #fff;
          &:hover{
            background-color: #fff;
            color: #ff6000;
          }
        }
      }
    }
  }
  .main-page__gallery{
    .gallery{
      .gallery-items{
        width: 100%;
        margin: 10px 0;
      }
    }
  }
}

@media only screen and (max-width: 500px) {

}

@media only screen and (max-width: 320px) {
  .main__top-button{
    .show-button{
      width: 100%;
      padding: 10px 0;
    }
  }
  .main-page__tabs, .main-page__tabs_first {
    .title__tabs{
      padding: 0;
      ul{
        li{
          width: 100%;
          a{
            font-size: 20px;
            padding: 10px 0;
          }
        }
      }
    }

  }

  .main-page__gallery{
    h2{
      font-size: 30px;
    }
  }
}
