.navigation {
	float: left;
	width: 100%;
	background-color: #191919;
	-webkit-box-sizing: border-box;
	   -moz-box-sizing: border-box;
	        box-sizing: border-box;
	&__menu_1 {
		// float: left;
		// padding: 0;
		// margin: 0;
		// li {
		// 	float: left;
		// 	// border-left: 1px solid #252424;
		// 	a {
		// 		display: block;
		// 		padding: 35px 15px;
		// 		color: #FFF;
		// 		&:hover {
		// 			background-color: #252424;
		// 		}
		// 	}
		// }

	}

	.navigation__menu_2, .navigation__menu_mobile {
		float: right;
		padding: 0;
		margin: 0;
		li {
			float: left;
			// border-left: 1px solid #252424;
			a {
				display: block;
				padding: 35px 15px;
				color: #FFF;
				&:hover {
					background-color: #252424;
				}
			}
		}
	}
	.navigation__menu_mobile{
		display: none;
		li{
			a{
				padding: 15px;
			}
		}
	}

	&__btn {
		display: none;
		float: right;
		size: 36px 30px;
		margin: 15px 0;
		position: relative;
		&:before {
			content: "";
			position: absolute;
			top: 0;
			width: 100%;
			height: 2px;
			background-color: #FFF;
		}
		&:after{
			content: "";
			position: absolute;
			bottom: 0;
			width: 100%;
			height: 2px;
			background-color: #FFF;
		}
		span {
			size: 75% 2px;
			display: block;
			position: absolute;
			top: 50%;
			left: 50%;
			-webkit-transform: translate(-50%, -50%);
			   -moz-transform: translate(-50%, -50%);
			    -ms-transform: translate(-50%, -50%);
			     -o-transform: translate(-50%, -50%);
			        transform: translate(-50%, -50%);
			background-color: #FFF;
			@include transition(.3s);
		}
		&-active {
			span {
				width: 100%;
			}
		}
	}
	&__search {
		float: left;
		width: 64%;
		height: 35px;
		margin: 27px 0;
		@include border-radius(5px);
		-webkit-box-sizing: border-box;
		   -moz-box-sizing: border-box;
		        box-sizing: border-box;
		background-color: #fff;
		padding: 0 10px 0 15px;
		&_icon {
			padding: 10px 0;
			float: right;
			svg {
				size: 14px;
				fill: #c7c7cc;
			}
		}
		&_box {
			border: none;
			outline: none;
			font-size: 14px;
			height: 33px;
			-webkit-box-sizing: border-box;
			   -moz-box-sizing: border-box;
			        box-sizing: border-box;
		}
	}
}

.navigation__menu_1{
	float: left;
	width: 15%;
	list-style: none;
	padding: 0;
	margin: 0;
	text-align: center;
}
.navigation__menu_1 li{
	float:none;
	width: 100%;
	position:relative;
	display: inline-block;
}
.navigation__menu_1 li a{
	display:block;
	display: block;
	padding: 35px 15px;
	box-sizing: border-box;
	color:#fff;
	text-transform: uppercase;
	text-decoration:none;

	text-shadow:1px 1px 1px rgba(0,0,0,0.75); /*  ,      */
	-moz-border-radius:2px;
	-webkit-border-radius:2px;
	border-radius:2px;
}
.navigation__menu_1 li a:hover{
	color:#fff;
	background-color: #252424;
	// background:rgba(107,12,54,0.75); /*   */
	text-decoration:underline;
}

/*---   ---*/
.navigation__menu_1 ul{
	list-style:none;
	position:absolute;
	z-index: 2;
	padding: 0;
	left:-9999px; /*   ,    (  ,  display:none;) */
	opacity:0; /*     */
	-webkit-transition:0.25s linear opacity; /*  Webkit     */
}
.navigation__menu_1 ul li{
	padding-top:1px; /*    li       */
	float:left;
	width: 100%;
}
.navigation__menu_1 ul a{
	white-space:nowrap; /*         */
	display:block;
	padding: 10px 15px;
	box-sizing: border-box;
	font-size: 14px;
}
.navigation__menu_1 li:hover ul{ /*       */
	left:0; /*     ,   */
	opacity:1; /*   */
}
.navigation__menu_1 li:hover a{ /*     ,     */
	background-color: #252424;
	// background:rgba(107,12,54,0.75); /*   */
	text-decoration:underline;
}
.navigation__menu_1 li:hover ul a{ /*          */
	text-decoration:none;
	-webkit-transition:-webkit-transform 0.075s linear;
}
.navigation__menu_1 li:hover ul li a:hover{ /*     ,       */
	// background:#333;
	// background:rgba(51,51,51,0.75); /*   */
	text-decoration:underline;
	-moz-transform:scale(1.05);
	-webkit-transform:scale(1.05);
}
