.pa_exit{
  position: relative;
  min-width: 92px;
}
.clearfix{
  clear: both;
}
.pa_bg_exit{
  display: block;
  margin-right: 15px;
  background: url("/img/log-out.png");
  width: 28px;
  height: 20px;
  background-repeat: no-repeat;
  -webkit-background-size:cover;
  background-size:cover;
  float: left;
  margin-right: 15px;


}
.pa_personal-area{
  position: relative;
  margin-left: 56px;
}
.pa_bg_personal-area{
  display: block;
  position: relative;
  background: url("/img/avatar.png");
  width: 19px;
  height: 24px;
  background-repeat: no-repeat;
  -webkit-background-size:cover;
  background-size:cover;
  top: 0;
  margin-right: 15px;
  float: left;
}
.personal_area{
  font-family: $gill;
  font-size: 18px;
  color: #000;
  float: left;
  margin-top: 24px;
}
.pa_section{
  font-family: $gill;

}
.pa_personal p {
  font-size: 14px;
}

.pa{
  display: block;
  float: left;
  width: 40%;
  padding: 35px 10px;
  box-sizing: border-box;
  float: left;

}
.pa-little{
  display: block;
  float: left;
  width: 20%;
  padding: 35px 10px;
  box-sizing: border-box;
  float: left;

}
.name_edit{
  float: left;
  width: 100%;
  display: inline-block;
  text-align: left;
  padding: 15px;
  background-color: #fdfdfd;
  box-sizing: border-box;
  margin-top: 0;
}
.edit {
  float: right;
  width: 40%;
  text-align: right;
  text-decoration: underline;
}
.pa_section{
  width: 100%;
  float: left;
  display: block;
  position: relative;
}
.photo-container{
  width: 40%;
  height: 137px;
  @include overflow-img;
  float: left;
}
.biker-info{
  float: right;
  width: 53%;
  padding-left: 15px;
}
.biker-info p{
  margin-top: 0;
}
.garage-add-bike{
  width: 39%;
  float: left;
  margin-top: 26px;
  padding-left: 14px;
}
.garage-add-bike-button{
  float: right;
  width: 54%;
}
.moto-photo, .owl-bike{
  width: 50%!important;
  height: 120px;
  float: left;
  overflow: hidden;
  position: relative;
  .slide-bike{
    width: 100%;
    display: block;
    position: relative;
    height: 120px;
    float:left;
    position: relative;
    img{
      width: 100%;
      position: absolute;
    }
  }
}
.bike-info{
  float: right;
  width: 40%;

}
.bike-info p{
  margin-top: 0;
}
.motocircle{
  margin: 20px;
}
.moto-photo, .bike-info{
  margin-top: 25px;
}
.border{
  border: 1px solid #000000;
  width: 100%;
}
.pa_events{

}

.pa_button{
  width: 200px;

}
.add_dalnak{
  float: right;
}
.pa_events>a{
  width: 170px;
}
.empty-block{
  width: 40%;
  float: left;

}
.comments{
  width: 60%;
  float: right;
  .new-comment{
    width: 99%;
    height: 76px;
    border-radius: 12px;
    resize: none;
    outline: none;
    border: 1px solid rgba(156, 156, 156, 0.38);
    padding-left: 15px;
  }
  .submit{
    border: transparent;
    width: 30%;
    outline: none;
  }
  .all-comments{
      h4{
        font-family: $gill;
        text-transform: uppercase;
        font-size: 14px;
        font-weight: 600;
      }
    .comment{
      width: 80%;
      float: left;
      padding-bottom: 33px;
    }
    .comment-date{
      float: right;
      padding-bottom: 33px;
    }
  }

}
.comments-button{
  margin-left: 15px;
  width: 170px;

}
.pa_events{
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: space-between;
}
.events-item-left, .events-item-right{
  display: block;
  position: relative;
  width: 50%;
  float: left;
  h4{
    font-family: $gill;
    font-size: 14px;
    line-height: 44px;
    display: block;
    position: relative;
    width: 100%;
    float: left;
    margin: 0;
    padding-left: 28px;
  }
  ul{
    padding: 0;

    display: block;
    position: relative;
    width: 100%;
    li{
      text-align: center;
      a:hover{
        color:#ff6000;
        -webkit-transition: all ease-in-out 0.2s;
        -moz-transition: all ease-in-out 0.2s;
        -ms-transition: all ease-in-out 0.2s;
        -o-transition: all ease-in-out 0.2s;
        transition: all ease-in-out 0.2s;
      }
    }

  }
  p{
    display: block;
    position: relative;
    width: 100%;
    margin: 0;
    padding-left: 28px;
  }
  a{
    color: #000;
  }
}
.events-control{
  column-count: 2;
  a{
  width: 180px;
}}
.garage-form__in-garage_items{
  width: 100%;
  float: left;
}
