.main__top-button{
  width: 100%;
  float: left;
  position: relative;
  text-align: center;
  background-color: #f4f4f4;
  .show-button{
    display: inline-block;
    font-size: 36px;
    font-family: $fow;
    color: #282828;
    text-decoration: none;
    float: none;
    margin: 0 auto;
    padding: 60px 0;
    box-sizing: border-box;
    min-width: 33%;
    position: relative;
    &:nth-of-type(2){
      background-color: #fff;
    }
  }

}


.route {
	float: left;
	width: 100%;
	position: relative;
	background-image: url('../img/asfalt.jpg');
	&__wrap {
		float: left;
		position: relative;
		width: 100%;
		height: 330px;
		background-image: url('../img/marking-1.png');
		-webkit-background-size: 100%;
		     -o-background-size: 100%;
		        background-size: 100%;
		background-repeat: no-repeat;
		background-position: center;
		padding: 15px 0;
	}
	&__text {
		float: left;
		width: 50%;
		color: #FFF;
		-webkit-box-sizing: border-box;
		   -moz-box-sizing: border-box;
		        box-sizing: border-box;
		padding-right: 50px;
		margin-bottom: 100px;
		margin-top: 50px;
		p {
			font-size: 16px;
		}
		h3 {
			color: #FFF;
			font-size: 32px;
			position: relative;
			margin-top: 0;
			&:after {
				content: "";
				position: absolute;
				bottom: -15px;
				left: 0;
				width: 95px;
				height: 0;
				border-bottom: 2px dashed #fff;
			}
		}
	}
	&__destination {
		float: left;
		position: relative;
		width: 25%;
		-webkit-box-sizing: border-box;
		   -moz-box-sizing: border-box;
		        box-sizing: border-box;
		display: flex;
		-webkit-flex-wrap: wrap;
		    -ms-flex-wrap: wrap;
		        flex-wrap: wrap;
		-webkit-align-content: flex-start;
		        align-content: flex-start;
		-webkit-justify-content: space-between;
		        justify-content: space-between;
		-webkit-flex-direction: column;
		    -ms-flex-direction: column;
		        flex-direction: column;
		height: 100%;
		padding: 90px 0;
		&_box {
			position: relative;
			display: block;
			float: left;
			width: 260px;
			height: 38px;
			@include border-radius(4px);
			background-color: #fff;
			-webkit-box-sizing: border-box;
			   -moz-box-sizing: border-box;
			        box-sizing: border-box;
			padding: 0 10px;
			margin-top: 35px;
		}
		&_inp {
			display: inline-block;
			border: none;
			outline: none;
			height: 33px;
			-webkit-box-sizing: border-box;
			   -moz-box-sizing: border-box;
			        box-sizing: border-box;
			width: 80%;
			&::-webkit-input-placeholder { text: $dark 10px; }
			&::-moz-placeholder { text: $dark 10px; } /* firefox 19+ */
			&:-ms-input-placeholder { text: $dark 10px; } /* ie */
			input:-moz-placeholder { text: $dark 10px; }
		}
		&_placeholder {
			position: absolute;
			text: #333 11px;
			top: 10px;
			pointer-events: none;
		}
	}
	&__go {
		float: left;
		width: 25%;
		-webkit-box-sizing: border-box;
		   -moz-box-sizing: border-box;
		        box-sizing: border-box;
		display: flex;
		-webkit-flex-direction: column;
		    -ms-flex-direction: column;
		        flex-direction: column;
		-webkit-flex-wrap: wrap;
		    -ms-flex-wrap: wrap;
		        flex-wrap: wrap;
		-webkit-justify-content: center;
		        justify-content: center;
		-webkit-align-items: flex-end;
		        align-items: flex-end;
		height: 100%;
		-webkit-align-content: center;
		        align-content: center;
		a.button {
			font-size: 14px;
			text-transform: uppercase;
			margin-top: 40px;
		}
	}
}
.main-page__tabs, .main-page__tabs_first{
  width: 100%;
  float: left;
  position: relative;
  background-color: #fff;
  .title__tabs{
    width: 100%;
    float: left;
    position: relative;
    background-color: #fff;

    ul{
      padding: 0;
      // background-color: #f4f4f4;
      margin: 0;
      li{
        width: 33.3%;
        float: left;
        list-style-type: none;
        // background-color: #f4f4f4;
        a{
          color: #282828;
          font-family: $fow;
          font-size: 36px;
          padding: 60px 0;
          box-sizing: border-box;
          text-transform: uppercase;
          text-decoration: none;
          background-color: #f4f4f4;
          span{
            border-bottom: 1px dashed $o;
            // min-width: 20%;
          }
        }
        &:nth-of-type(2){
          a{
            background-color: #fff!important;
          }
        }
      }
    }
  }

  .page__tabs_target, .page__tabs_target_first{
    width: 100%;
    float: left;
    display: inline-block;
    text-align: center;
  }


  .box-connent{
    width: 100%;
    float: left;
    position: relative;
  }

  .page__tabcontent, .page__tabcontent_first{
    width: 100%;
    float: left;
    // padding: 0 0 50px;
    box-sizing: border-box;
    .column{
      width: 21.4%;
      float: left;
      margin-right: 55px;
      &:last-of-type{
        margin-right: 0;
      }
      h3{
        color: $o;
        font-size: 21px;
        text-decoration: underline;
        font-family: $gill;
        padding-left: 20px;
        box-sizing: border-box;
      }
      .statistic__item{
        width: 100%;
        float: left;
        display: inline-block;
        border: 0.5px solid #898989;
        border-radius: 5px;
        padding: 10px 15px;
        box-sizing: border-box;
        &_wrapper{
          width: 100%;
          float: left;
          position: relative;
          margin: 10px ;
          p{
            margin: 0;
            font-size: 16px;
            font-family: $gill;
            font-weight: 600;
          }
        }
      }
    }
    .navigation__search_box{
      width: 93%;
    }
    .motoclub-content-promo{
      width: 100%;
    }
    .howitworks-hide{
    	display: block;
    	width: 100%;
      padding-top: 40px;
      box-sizing: border-box;
      p{
          text-align: center;
          margin: 0 auto 15px;
          display: block;
          width: 70%;
          color: #383838;
      }
    }
    .howitworks {
    	float: left;
    	width: 100%;
    	padding: 50px 0;
    	display: block;
    	&__flex {
    		display: flex;
    		-webkit-flex-wrap: wrap;
    		    -ms-flex-wrap: wrap;
    		        flex-wrap: wrap;
    		 -webkit-justify-content: space-between;
    		         justify-content: space-between;
    		    -webkit-align-items: flex-start;
    		            align-items: flex-start;
    	}
    	h3 {
    		position: relative;
    		text-align: center;
    		font-size: 36px;
    	}

    	&__item {
    		-webkit-flex-basis: 250px;
    		        flex-basis: 250px;
    	    margin: 25px 0;
    	    -webkit-box-sizing: border-box;
    	       -moz-box-sizing: border-box;
    	            box-sizing: border-box;
    	    padding: 10px 0;
    	    text-align: center;
        	img {
        		height: 100px;
        		width: 100%;
        		margin-bottom: 10px;
        		object-fit: contain;
        		object-position: center;
        	}
        	h4 {
        		font-size: 24px;
        		margin: 15px 0;
        	}
        	p {
        		font-size: 14px;
        		width: 100%;
        		margin: 0;
        	}
        	a {
        		float: left;
        		width: 100%;
        		margin: 15px 0;
        		text: #ff1d18 14px;
        		text-decoration: underline;
        	}
    	}
    }
    .motoclub-search{
      padding-top: 40px;
      box-sizing: border-box;
    }
    .motoclub-search-input{
      width: 97%;
      margin-bottom: 30px;

    }
    .travels-tab{
      .events-conrent__item{
        .events-conrent__item_thumb{
          height: 164px;
          img{
            height: 164px;
            object-fit: cover;
          }
        }
        .travels-item_title{
          font-family: $gill;
          font-size: 16px;
          color: #282828;
          text-transform: uppercase;
          text-decoration: none;
          font-weight: 700;
        }
      }
    }
    .events-conrent__box{
      width: 100%;
      float: left;
      justify-content: center;
      .events-conrent__item{
        padding-bottom: 50px;
        .button{
          width: 100%;
        }
      }
    }
    .news__box{
      padding-right: 0;
      padding-top: 40px;
      box-sizing: border-box;
      .news__item{
        flex-basis: 21%;
        h4{
          color: #ff6000;
          text-align: left;
        }
      }
    }
  }
  .page__tabcontent{
    .motoclub-search{
      padding-bottom: 0;
    }
    .travels-tab{
      padding-top: 25px;
      box-sizing: border-box;
    }
  }
  .two{
    display: block;
  }
  .one,.three{
    display: none;
  }
  .one_first,.two_first, .three_first{
    display: none;
    text-align: center;
  }
  .one_first{
    .add-article{
      width: 50%;
      background-color: #ff6000;
      color: #fff;
      &:hover{
        background-color: #fff;
        color: #ff6000;
      }
    }
  }

  .page__tabs_active{
    display: block;
    span{
      color: $o;
    }
  }
  .page__tabs_first_active{
    display: block;
    span{
      color: $o;
    }
  }

}

.main-page__gallery{
  width: 100%;
  float: left;
  position: relative;
  padding: 50px 0;
  box-sizing: border-box;
  background-color: #fff;
  h2{
    display: inline-block;
    width: 100%;
    text-align: center;
    font-size: 36px;
    color: #282828;
    font-family: $fow;
    text-transform: uppercase;
    position: relative;
    &::after{
      content: "";
      border-bottom: 1px dashed $o;
      display: inline-block;
      position: absolute;
      min-width: 11%;
      left: 45%;
      bottom: 0;
    }
  }
  .gallery{
    .gallery-items{
      width: 14.5%;
      height: 177px;
      margin: 12px;
      display: inline-block;
      float: left;
      img{
        height: 100%;
        width: 100%;
        object-fit: cover;
      }
    }

  }
}
