.bikers{
  width: 100%;
  float: left;
  display: block;
  position: relative;
  padding: 45px 0;
  box-sizing: border-box;
  h2{
    font-size: 20px;
    color: #000;
    font-family: $gill;
  }
  &__form{
    width: 30%;
    float: left;
    display: inline-block;
    input{
      outline: none;
    }
  }
}
.bikers-all, .bikers-all-dop{
  width: 100%;
  float: left;
  position: relative;
  text-align: center;
  padding: 20px 0;
  box-sizing: border-box;


  display: block;
  //&__items{
  //  width: 33.3%;
  //  float: left;
  //  display: block;
  //  position: relative;
    &__items_user{
      width: 33.3%;
      margin: 15px auto;
      display: inline-block;
      float: left;
      .thumb{
        size:70px;
        position: relative;
        float: left;
        display: inline-block;
        img{
          position: absolute;
          width: auto;
          height: 100%;
          left: 0;
          top: 0;
        }
      }
      .text{
        width: 70%;
        float: left;
        display: inline-block;
        p{
          font-family: $gill;
          font-size: 18px;
          color: #000;
          text-align: left;
          padding-left: 10px;
          box-sizing: border-box;
          line-height: 30px;
        }
      }
    }
 // }
}
.show-allbikers{
  display: inline-block;
  float: left;
  min-width: 15%;
  color: #030000;
  font-family: $gill;
  font-size: 17px;
  font-weight: 400;
  line-height: 35px;
  margin: 0 auto;
  text-align: center;
  height: 38px;
  background-color: #ff6000;
  border: 1px solid rgba(156, 156, 156, 0.38);
  border-radius: 3px;
  box-shadow: inset 0 0 0 3px rgba(255, 255, 190, 0.75);
  position: absolute;
  bottom: 20px;
  left: 13%;
}
.bikers-all-dop{
  display: none;
}
